.custom-button {
  padding: 7px 5px;
  margin-top: 10px;
  background: #2ce080;
  color: white;
  border: 1px solid #2ce080;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  width: 100%;
  outline: none;
  transition: transform 100ms ease-in;

  &:hover {
    transform: scale(1.05);
  }
}

.google-sign-in {
  padding: 7px 5px;
  margin-top: 10px;
  background: white;
  color: #2962ff;
  border: 1px solid #2962ff;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
  outline: none;

  &:hover {
    background: #2962ff;
    color: white;
  }
}
